.calculator {
  width: 1000px;
  margin: 50px auto;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 10px;
  background-color: #3b3b3b;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  font-family: 'Roboto', sans-serif;
}

.display {
  font-size: 48px;
  margin-bottom: 20px;
  padding: 10px;
  background-color: #262626;
  border: 1px solid #ccc;
  border-radius: 5px;
  text-align: right;
  color: white;
}

.buttons {
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  gap: 8px;
}

.button {
  padding: 20px;
  font-size: 18px;
  background-color: #4d4d4d;
  border: 1px solid #ccc;
  border-radius: 5px;
  cursor: pointer;
  color: white;
}

.button:hover {
  background-color: #666666;
}
.button.numbers{

}
.button.operator {
  background-color: #f99e00;
  color: white;
}

.button.operator:hover {
  background-color: #ffa733;
}

 
